body
{
	--bg-color: #000000;
	--txt-color: #FFFFFF;
	--highlight-color: #FF6600;
	--alt-color: #B80C09;

	background-color: var(--bg-color);
	color: var(--txt-color);
	margin: 0px;
	font-family: 'Open Sans', sans-serif;
	font-size: 1.2vw;
}

pre
{
	color: var(--txt-color);
	font-family: 'Open Sans', sans-serif;
	font-size: 1.2vw;
}

a
{
	color: var(--alt-color);
	font-weight: bold;
}

u
{
	text-underline-position: under;
}

b
{
	color: var(--highlight-color);
}

.document {
	height: 100%;
}

#TitleBar{
	width: 100%;

	position: relative;
}

#TitleImage
{
	width: 100%;

	vertical-align: top;
}

#Slider
{
	width: 0px;
	height: 98%;

	background-color: var(--highlight-color);

	z-index: -1;
	left: 20%;
	top: 1%;
	position: absolute;
    -webkit-animation: slideIn 4s 1 0s ease-out forwards;
            animation: slideIn 4s 1 0s ease-out forwards;
}

@-webkit-keyframes slideIn{
    0%{width: 0%;}
    100%{width: 60%;}
}

@keyframes slideIn{
    0%{width: 0%;}
    100%{width: 60%;}
}

#About-Section{
	width: 100%;
	height: 40vw;

	background-color: var(--highlight-color);

	text-align: center;
}

.particles{
	width: 100%;
	height: 40vw;

	position: absolute;
}

#About{
	width: 75%;
	height: 32vw;

	background-color: rgba(200, 200, 200, 0.5);

	display: inline-block;
	position: relative;

	margin: 4vw;
}

#About h1
{
	width: 60%;

	margin-right: 0px;
	margin-bottom: 0.6%;
}

#About p
{
	width: 60%;

	margin-left: 5%;
	margin-top: 0px;

	text-align: left;
}

#About b
{
	color: var(--alt-color);
}

#portrait
{
	width: 25%;

	float: right;
	border-radius: 100%;

	margin: 5%;
}

#Links
{
	width: 60%;
}

#Links button
{
  	background-color: var(--highlight-color);

	color: var(--txt-color);
	font-size: 2vw;

	border: none;
	border-radius: 0.7vw; /* 20vw */

	padding: 2%;
	margin: 0px 2%; 

	cursor: pointer;
	outline: 0;
}

#Links button:hover {
	background-color: #FFFFFF;

	color: #000000;
}

#Links img
{
	width: 1.5vw;
}

.content
{
	width: 100%;

	background-color: inherit;

	vertical-align: top;
	display: inline-block;
}

.contentItem
{
	display: flex;

	justify-content: center;
	opacity: 0;
  	
  	-webkit-animation-name: fadeIn;
  	
  	        animation-name: fadeIn;
	-webkit-animation-duration: 2s;
	        animation-duration: 2s;
	-webkit-animation-timing-function: ease-out;
	        animation-timing-function: ease-out;
	-webkit-animation-iteration-count: 1;
	        animation-iteration-count: 1;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
	-webkit-animation-play-state: paused;
	        animation-play-state: paused;
}

@-webkit-keyframes fadeIn
{
	0% {opacity: 0;}
	100% {opacity: 100;}
}

@keyframes fadeIn
{
	0% {opacity: 0;}
	100% {opacity: 100;}
}

.contentElement
{
	width: 70%;

	background-color: inherit;
	
	position: relative;

	border: 0.5vw solid var(--highlight-color);

	padding: 4%;
	margin-top: 2%;
	margin-bottom: 2%;
}

.contentElement h1
{
	text-align: center;

	margin: 0px;
}

.contentElement pre
{
	width: 65%;

    white-space: pre-wrap;
    white-space: -moz-pre-wrap;
    white-space: -pre-wrap;
    white-space: -o-pre-wrap;
    word-wrap: break-word; 
}

svg
{
	width: 100%;
	height: auto;
	
	z-index: -1;
	position: absolute;
}

.line-box
{
	stroke-dasharray: 1000;
  	-webkit-animation: flow 3s 1 linear;
  	        animation: flow 3s 1 linear;
}

.lines
{
	stroke: var(--highlight-color);
	stroke-width: 0.3vw;
}

@-webkit-keyframes flow
{
	0% {stroke-dashoffset: 1000;}
	100% {	stroke-dashoffset: 0;}
}

@keyframes flow
{
	0% {stroke-dashoffset: 1000;}
	100% {	stroke-dashoffset: 0;}
}
